export default{
	data(){
		return{
			formData: Object,
			imageList: [],
			isWork: false,
			result: Object,
		}
	},
	computed:{
		Wheight(){
			return uni.getSystemInfoSync().windowHeight+'px'
		},
		fans() {
			return this.$store.state.fans
		}
	},
	methods:{
		async _WorkAdd(){
			if(!this.formData.content || !this.formData.pictures){
				this.ShowToast('请填写内容',1000,false,'none')
				return
			}
			const work = await this.$http.WorkAdd({content:this.formData.content,pictures:this.formData.pictures})
			this.result = work.data.result.info
			if(work.data.code==0){
				await this.$store.commit('getFans', {...this.fans, punch:this.fans.punch-1})
				this.isWork = !this.isWork
			}
		},
		chooseImage() {
			uni.chooseImage({
				count: 1,
				sizeType: ['original','compressed'],
				sourceType: ['album','camera'],
				success: (res) => {
					uni.showLoading({title:'上传中,请稍后...',mask:true})
					var imageSrc = res.tempFilePaths[0]
					uni.uploadFile({
						url: _config.url+'/v1/upload/images',
						filePath: imageSrc,
						fileType: 'image',
						name: 'file[0]',
						formData: {
							'type': 'work'
						},
						success: (res) => {
							let data = JSON.parse(res.data)
							if(data.code==0){
								uni.hideLoading()
							}
							this.imageList.push(imageSrc)
							this.formData.pictures = data.result.list.map(item=>{
								return item.file
							})
						},
						fail: (err) => {
							uni.hideLoading()
							uni.showModal({
								content: err.errMsg,
								showCancel: false
							});
						}
					});
				},
				fail: (err) => {
					console.log('chooseImage fail', err)
				}
			})
		},
		previewImage(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		close() {
			this.imageList = []
		},
		goconfirm() {
			this.isWork = !this.isWork
			uni.navigateBack()
		}
	}
}
